body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 40px;
    font-weight: bold;
    line-height: 50px;
    height: 50px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 50px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
  }
  
  .win-field {
    background-color: #99999940;
  }

  
  .historyView {
    width: 75px;
    height: 75px;
    margin: 10px;
  }

  .historyCell {
    width: 25px;
    height: 25px;
    text-align: center;
    border: 1px solid black;
    box-sizing: border-box;
    line-height: 25px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    float: left;
  }

  .historyStep {
    display: inline-block;
    margin-right: 20px;
  }

  .current-cell {
    border-color: red;
  }

  .status {
    margin: 15px;
    font-size: 25px;
  }